.contenedor
{
    width: 1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffff;
    box-shadow: 0 3px 13px 1px rgb(0 0 0/ 9%);

}

.imagen-contenedor
{
    height: 100%;
    width: auto;
}

.contenedor-texto
{
    text-align: center;
    padding: 40px;
    font-family: Lato, sans-serif;
    font-weight: 400;
    line-height: 1.5rem;

}
.nombre
{
    font-size: 1.3rem;
}

.grado
{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}

.texto 
{
    font-size: 1.1rem;
    text-align: justify;
}
